/* ################# about us ######################### */

#about{
    padding: 50px 0px;
    background: #f5f5f7;

}

.about-wrapper{
    display: flex;
    flex-wrap: wrap;

}

#about h2{
    font-size: 3.2rem;
}
#about p{
    font-size: 1.6rem;
    color: #555;

}
