/* ########################## course category styling ###################### */

#IT{
    padding: 5rem 0 10rem 0;

}
#IT .Tushyap{
    text-align: center;
    font-size: 3rem;
    font-weight: 400;
    margin-bottom: 40px;
    text-transform: uppercase;
    color: #555;
}
.web-container{
