   margin: auto;
   max-height: 300px;
   object-fit: cover;
   object-position: center;

}
.img-container{
    margin: 0 1rem;
    position: relative;

}
.img-content{

    position: absolute;
    top: 70%;
    left: 50%;
    transform: translate(-50%, -50%);
    opacity: 0;
    z-index: 2;
    text-align: center;
    transition: all 0.3s ease-in-out 0.1s;
}

.img-content h3{
    color: #fff;
    font-size: 2rem;

}

.img-content a{
    font-size: 1.2rem;
}
.img-container::after{
    content: "";
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,0.871);
    opacity: 0;
    z-index: 1;
    transform: scaleY(0);
    transform-origin: 100% 100%;
    transition: all 0.3s ease-in-out;

}
.img-container:hover::after{
   opacity: 1;
   transform: scaleY(1);   
}

.img-container:hover .img-content{
    opacity: 1;
    top: 40%;

}
