body {
    margin: 0;
    height: 100vh;
    width: 100vw;
    overflow: hidden;
    font-family: "Lato", sans-serif;
    font-weight: 500;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #555;
    background-color: #ecf0f3;

  }
  
  * {
    box-sizing: border-box;
  }
  
  .row { 
    display: flex; 
    flex-wrap: wrap;
    margin: 0 -16px;
  }
  
  .col-25 { 
    flex: 25% 1;
  }
  
  .col-50 { 
    flex: 50% 1;
  }
  
  .col-75 { 
    flex: 75% 1;
  }
  
  .col-25,
  .col-50,
  .col-75 {
    padding: 0 16px;
  }
  .mainDiv{
     max-width: 1400px;
    width: 100%;
    height: 700px;
    padding: 40px 25px 25px 25px;
    border-radius: 40px;
    background-color: #ecf0f3;
    box-shadow: 13px 13px 20px #cbced1,
    -13px -13px 20px #fff;
}
  .mainHeading{
      margin: 0px;
      text-align: center;
      font-size: 30px;
      padding-top: 7px;
      letter-spacing: 1px;
      text-transform: uppercase;
      
  }
  .container {
    /* background-color: #f2f2f2; */
    padding: 5px 20px 15px 20px;
    /* border: 1px solid lightgrey; */
    border-radius: 3px;
  }
  
  input[type=text] {
     width: 100%;
    margin-bottom: 20px;
    padding: 12px;
    height: 40px;
    font-size: 13px;
    letter-spacing: 0.15px;
    border: none;
    outline: none;
    background-color: #ecf0f3;
    transition: 0.25s ease;
    border-radius: 8px;
    box-shadow: inset 2px 2px 4px #d1d9e6, inset -2px -2px 4px #f9f9f9;
  }
  input[type=text]:focus{
    box-shadow: inset 4px 4px 4px #d1d9e6, inset -4px -4px 4px #f9f9f9;
  }
  
  label {
    margin-bottom: 10px;
    display: block;
  }
  
  .icon-container {
    margin-bottom: 20px;
    padding: 7px 0;
    font-size: 24px;
  }
  
  .signin-button{
    outline: none;
    border: none;
    cursor: pointer;
    width: 100%;
    height: 60px;
    border-radius: 30px;
    font-size: 20px;
    font-weight: 700;
    font-family: "Lato",sans-serif;
    color: #fff;
    text-align: center;
    background-color: #02c8db;
    box-shadow: 3px 3px 8px #b1b1b1,
    -3px -3px 8px #fff;
    transition: all 0.5s;
}
.signin-button a{
    text-decoration: none;
    color: #fff;
    font-size: 20px;

}
.signin-button:hover{
    background-color: #50e5b9;
}
.signin-button:active{
    background-color: #88ef9e;
}
  
  a {
    text-decoration: none;
    color: #555;
  }
  
  hr {
    border: 1px solid lightgrey;
  }
  
  span.price {
    float: right;
    color: grey;
  }
  
 
